// 接口请求封装
import request from '@/utils/request.js'

// 登录接口
export const loginAPI = ({mobile,code}) => request({
    url: '/v1_0/authorizations',
    method: 'post',
    data: {
        mobile,
        code 
    }
})

// 频道 - 获取所有频道
export const getAllChannelsAPI = () => request({
    url: '/v1_0/channels',
    method: 'get'
})

// 频道 - 获取用户选择的频道
// 注意：用户没有登陆，默认返回后台设置的默认频道列表
export const getUserChannelsAPI = () => request({
    url: '/v1_0/user/channels',
    method: 'get',
})

// 频道 - 更新覆盖频道
export const updateChannelsAPI = ({channels}) => request({
    url: '/v1_0/user/channels',
    method: 'put',
    data: {
        channels // 用户已选整个频道数组
    }
})

// 频道 - 删除用户指定的频道
export const removeTheChannelAPI = ({channelId}) => request({
    url: `/v1_0/user/channels/${channelId}`,
    method:'delete'
})

// 文章 - 获取新闻文章列表
export const getAllArticleListAPI = ({ channel_id, timestamp }) => request({
    url: '/v1_0/articles',
    method: 'get',
    params: {
        channel_id,
        timestamp
    }
})

// 文章 - 对文章不喜欢反馈接口
export const dislikeArticleAPI = ({artId}) => request({
    url: '/v1_0/article/dislikes',
    method: 'post',
    data: {
        target:artId
    }
})

// 文章 -文章反馈举报接口
export const reportArticleAPI = ({artId,type}) => request({
    url: '/v1_0/article/reports',
    method: 'post',
    data: {
        target: artId,
        type: type,
        remark:'其他问题的附加说明'
    }
})

// 搜索 - 联想菜单
export const suggestListAPI = ({keywords}) => request({
    url: '/v1_0/suggestion',
    method: 'get',
    params: {
        q:keywords
    }
})

// 搜索 - 搜索结果列表
export const SearchResultAPI = ({ page, per_page ,q}) => request({
    url: '/v1_0/search',
    method: 'get',
    params: {
        page,
        per_page,
        q
    }
})

