// 文章详情 - 获取评论列表
export const commentsListAPI = ({ id, offset = null,limit = 10}) => request({
    url: '/v1_0/comments',
    method: 'get',
    params: {
        type: 'a',
        source: id,
        offset,
        limit
    }
})

 // 文章详情 - 评论 - 喜欢
export const commentLikingAPI = ({ comId }) => request({
    url: '/v1_0/comment/likings',
    method: 'post',
    data: {
        target: comId
    }
})

 // 文章详情 - 评论 - 取消喜欢
export const commentDisLikingAPI = ({ comId }) => request({
    url: `/v1_0/comment/likings/${comId}`,
    method: 'delete',
})

// 文章详情 - 发布评论
export const commentSendAPI = ({ id, content, art_id = null }) => {
    const obj = {
        target: id,
        content,
    }
    if (art_id !== null) {
        obj.art_id = art_id
    }
    return request({
        url: '/v1_0/comments',
        method: 'post',
        data: obj,
    })
}

// 用户 - 获取基本信息(我的页面显示数据)
export const getUserInfoAPI = () => request({
    url: '/v1_0/user',
    method: 'get'
})

// 用户 - 获取个人资料(编辑页面使用)
export const userProfileAPI = () => request({
    url: '/v1_0/user/profile',
    method: 'get'
})

// 用户 - 更新头像
export const updateUserPhotoAPI = (fd) => request({
    url: '/v1_0/user/photo',
    method: 'patch',
    data: fd  // fd外面一会儿传进来的new FormData() 表单对象
})

// 用户 - 更新基本资料
export const updateUserProfileAPI = (dataObj) => {
    const obj = {
        name: '',
        gender: 0,
        birthday: '',
        intro:''
    }
    for (const prop in obj) {
        if (dataObj[prop] === undefined) {
            delete obj[prop]
        } else {
            obj[prop] = dataObj[prop]
        }
    }
    return request({
        url: '/v1_0/user/profile',
        method: 'patch',  // 局部更新 -> put（全部更新）
        data: dataObj
        // {
        //     name,
        //     gender,
        //     birthday,
        //     intro
        // }
    })
}

// 用户 - 刷新token
export const getNewTokenAPI = () => request({
    url: '/v1_0/authorizations',
    method: 'put',
    headers: {
        Authorization: 'Bearer ' + localStorage.getItem('refresh_token')
    }
})