// axios封装
import theAxios from "axios";
import router from '@/router'
import { Notify } from 'vant'
import { getToken, removeToken, setToken } from '@/utils/token.js'
import { getNewTokenAPI } from '@/api'


const axios = theAxios.create({
    baseURL: 'http://toutiao.itheima.net',
    timeout: 20000
})

// 请求拦截器
axios.interceptors.request.use((config) => {
    if (getToken()?.length > 0 && config.headers.Authorization === undefined) {
        config.headers.Authorization = `Bearer ${getToken()}`
    }
    return config
}, (error) => {
    return Promise.reject(error)
})

// 响应拦截器
axios.interceptors.response.use((response) => { 
    return response
}, async (error) => {
    if (error.response.status === 401) {
        removeToken()  // 先清除token
        
        // 方式一 ：清除token,强制跳转到登录，用户有感知
        // router.replace('/login')

        // 方式二 ： 使用refresh_token换回新的token在继续使用，用户无感知
        const res = await getNewTokenAPI()
        setToken(res.data.data.token)
        error.config.headers.Authorization = `Bearer ${res.data.data.token}`
        return axios(error.config)
    } else if (error.response.status === 500 && error.config.url === '/v1_0/authorizations' && error.config.method === 'put') {
        // 刷新的refresh_token也过期了
        localStorage.clear()  // 清除本地所有的
        Notify({ type: 'warning', message: '身份已过期' })
        // router.request(`/login?path=${router.currentRoute.fullPath}`)
        router.replace('/login')
    }
    return Promise.reject(error)
})
export default ({ url, method = 'get', params = {}, data = {}, headers = {} }) => {
    return axios({
        url,
        method,
        params,
        data,
        headers
    })
}
